<template>
  <div id="sign">
    <div class="signInWarp" v-show="signShow">
      <div class="upBgWarp"></div>
      <div class="signIn">
        <div class="signText">用户登录</div>
        <div class="inputWarp">
          <el-input placeholder="请输入账号" v-model="input1" clearable class="itemInput">
            <template slot="prepend">账号</template>
          </el-input>
          <i v-if="input1E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input1S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input placeholder="请输入密码" type="password" v-model="input2" clearable class="itemInput">
            <template slot="prepend">密码</template>
          </el-input>
          <i v-if="input2E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input2S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div class="inputWarp">
          <el-input placeholder="请输入验证码" v-model="input3" minlength="4" maxlength="4" clearable class="itemInput">
            <template slot="prepend">
              <img :src="imgSrc" @click="verificationClickFn" alt="" style="display: inline-block;">
            </template>
          </el-input>
          <i v-if="input3E" class="el-icon-close inputState inputStateE"></i>
          <i v-if="input3S" class="el-icon-check inputState inputStateS"></i>
        </div>
        <div style="text-align: right;">
          <el-button type="text" @click="fixPassWordFn">修改密码</el-button>
        </div>
        <el-button type="primary" @click="signClickFn" :disabled="openBtn">登录</el-button>
      </div>
    </div>
    <el-dialog
            title="设置参数"
            width="550px"
            :visible.sync="dialogFormVisible"
            :close-on-click-modal="clickM"
            @close="closeDialog"
            class="setDiaLog">
      <el-form :model="form">
        <el-form-item label="信令" :label-width="formLabelWidth">
          <el-input v-model="form.lingpai" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="窗口号" :label-width="formLabelWidth">
          <el-input v-model="form.window" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelFn">取 消</el-button>
        <el-button type="primary" @click="sureFn">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { baseUrl } from '@/api/unsplash/baseUrl'
import { mapActions } from 'vuex'
import store from '@/store/index'
// import supportFun from '@/supportFunction/index'
export default {
  name: 'Sign',
  store,
  data () {
    return {
      toNext: '/reference/theme',
      signShow: true,
      input1: '',
      input1E: false,
      input1S: false,
      input2: '',
      input2E: false,
      input2S: false,
      input3: '',
      input3E: false,
      input3S: false,
      imgSrc: '',
      code_id: '',
      baseUrl: baseUrl,
      isIdentify: false,
      openBtn: true,

      clickM: false,
      fixedParameters: {},
      dialogFormVisible: false,
      form: {
        lingpai: '123',
        window: '18'
      },
      formLabelWidth: '120px'
    }
  },
  watch: {
    input3: function (val) {
      this.input3 = val.replace(/(^\s*)|(\s*$)/g, '')
      if (this.input3.length === 4) {
        // 验证
        this.identifyfn()
      } else {
        this.input3E = false
        this.input3S = false
        this.isIdentify = false
      }
    },
    isIdentify: function (val) {
      this.isIdentify = val
      if (this.isIdentify && this.input1 !== '' && this.input2 !== '') {
        this.openBtn = false
      } else {
        this.openBtn = true
      }
    },
    input1: function (val) {
      this.input1 = val
      if (this.isIdentify && this.input1 !== '' && this.input2 !== '') {
        this.openBtn = false
      } else {
        this.openBtn = true
      }
    },
    input2: function (val) {
      this.input2 = val
      if (this.isIdentify && this.input1 !== '' && this.input2 !== '') {
        this.openBtn = false
      } else {
        this.openBtn = true
      }
      if (this.input2E) {
        this.input2E = false
        this.input2S = false
      }
    }
  },
  created () {
    this.loadImgFn()
  },
  mounted () {
    this.sessionFn()
  },
  methods: {
    ...mapActions({add_Routes: 'add_Routes'}),
    randomPassword (size) {
      let seed = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'V', 'W', 'X', 'Y', 'Z', 'a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'm', 'n', 'p', 'Q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z', '2', '3', '4', '5', '6', '7', '8', '9']
      let seedlength = seed.length
      var createPassword = ''
      for (let i = 0; i < size; i++) {
        createPassword += seed[Math.floor(Math.random() * seedlength)]
      }
      return createPassword
    },
    loadImgFn () {
      this.code_id = this.randomPassword(24)
      this.imgSrc = this.baseUrl + '/codes?code_id=' + this.code_id
    },
    verificationClickFn () { // click change verification
      this.loadImgFn()
    },
    signClickFn () { // click to signIn
      this.signInfn()
    },
    fixPassWordFn () {
      this.$router.push({path: '/fix'})
    },
    cancelFn () {
      this.dialogFormVisible = false
    },
    sureFn () {
      let temp = this.form
      if (temp.lingpai !== '' && temp.window !== '') {
        localStorage.lingpai = this.form.lingpai
        localStorage.window = this.form.window
        let userInfo = JSON.parse(sessionStorage.userInfo)
        userInfo.lingpai = this.form.lingpai
        userInfo.window = this.form.window
        // 工作人员信息2.2储存
        sessionStorage.userInfo = JSON.stringify(userInfo)
        this.setLingPaifn({
          lingpai: localStorage.lingpai,
          win: localStorage.window,
          user_id: sessionStorage.user_id
        })
      } else {
        this.notifyFn('提示', '请补全信息')
      }
    },
    closeDialog () {
      this.dialogFormVisible = false
    },
    async sessionFn (params) {
      try {
        let res = await this.$api.unsplash.sessionExist(params)
        if (res.errno === '0') {
          alert(res.errno)
          if (res.data) {
            res.data.user_id = res.data.id
            let temp = []
            res.data.permissions.forEach(function (val, index) {
              temp.push(val.all_discripe)
            })
            res.data.allPermission = temp
            // 工作人员信息 1.1储存
            sessionStorage.userInfo = JSON.stringify(res.data)
            // 单独存储工作人员ID
            sessionStorage.user_id = res.data.id
            store.commit('changeUserInfo')
            store.commit('changeBackAside')
            if (res.data.role === '大厅') {
              if (localStorage.lingpai && localStorage.window) {
                res.data.lingpai = localStorage.lingpai
                res.data.window = localStorage.window
                // 登录成功
                // 工作人员信息 2.1储存
                sessionStorage.userInfo = JSON.stringify(res.data)
                store.commit('changeUserInfo')
                this.setLingPaifn({
                  lingpai: localStorage.lingpai,
                  win: localStorage.window,
                  user_id: sessionStorage.user_id
                })
              } else {
                // 设置参数
                this.dialogFormVisible = true
                this.form = {
                  lingpai: localStorage.lingpai || '',
                  window: localStorage.window || ''
                }
              }
            } else {
              this.$router.push({path: '/reference/theme'})
            }
          }
        } else if (res.errno === '4004') {
          alert(res.errno)
          this.input2E = true
          this.input2S = false
          this.notifyFn('提示', res.errmsg)
        } else {
          alert(res.errno)
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async signInfn () {
      try {
        let params = {
          username: this.input1,
          password: this.input2
          // lingpai: localStorage.lingpai || '',
          // win: localStorage.window || ''
        }
        let res = await this.$api.unsplash.signIn(params)
        if (res.errno === '0') {
          if (res.data) {
            res.data.user_id = res.data.id
            let temp = []
            res.data.permissions.forEach(function (val, index) {
              temp.push(val.all_discripe)
            })
            res.data.allPermission = temp
            // 工作人员信息 1.1储存
            sessionStorage.userInfo = JSON.stringify(res.data)
            // 单独存储工作人员ID
            sessionStorage.user_id = res.data.id
            store.commit('changeUserInfo')
            store.commit('changeBackAside')
            if (res.data.role === '大厅') {
              if (localStorage.lingpai && localStorage.window) {
                res.data.lingpai = localStorage.lingpai
                res.data.window = localStorage.window
                // 登录成功
                // 工作人员信息 2.1储存
                sessionStorage.userInfo = JSON.stringify(res.data)
                store.commit('changeUserInfo')
                this.setLingPaifn({
                  lingpai: localStorage.lingpai,
                  win: localStorage.window,
                  user_id: sessionStorage.user_id
                })
              } else {
                // 设置参数
                this.dialogFormVisible = true
                this.form = {
                  lingpai: localStorage.lingpai || '',
                  window: localStorage.window || ''
                }
              }
            } else {
              this.$router.push({path: '/reference/theme'})
            }
          }
        } else if (res.errno === '4004') {
          this.input2E = true
          this.input2S = false
          this.notifyFn('提示', res.errmsg)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async setLingPaifn (params) {
      try {
        let res = await this.$api.unsplash.setLingPai(params)
        if (res.errno === '0') {
          // 设置成功及登录成功
          this.dialogFormVisible = false
          let obj = {
            radio: '1',
            curTheme: '',
            curMatter: '',
            selectionElementId: [],
            allID: []
          }
          sessionStorage.reference = JSON.stringify(obj)
          sessionStorage.initNum = 1
          this.$router.push({path: '/reference/theme'})
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async identifyfn () {
      try {
        let params = {
          'captcha': this.input3,
          'code_id': this.code_id
        }
        let res = await this.$api.unsplash.identify(params)
        console.log(res)
        if (res.msg === '通过') {
          this.input3E = false
          this.input3S = true
          this.isIdentify = true
        } else if (res.msg === '验证码错误') {
          this.input3E = true
          this.input3S = false
          this.isIdentify = false
        } else if (res.msg === '验证码过期') {
          this.notifyFn('提示', res.msg)
          let self = this
          setTimeout(function () {
            self.input3 = ''
            self.input3E = false
            self.input3S = false
            self.isIdentify = false
            self.loadImgFn()
          }, 300)
        } else {
          this.input3E = true
          this.input3S = false
          this.isIdentify = false
          this.notifyFn('提示', '操作错误')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    notifyFn (title, msg) {
      this.$notify({
        title: title,
        message: msg,
        type: 'warning',
        dangerouslyUseHTMLString: true,
        duration: 5000,
        position: 'top-right'
      })
    }
  }
}
</script>
<style lang="scss" scoped>
  #sign{
    .signInWarp{
      width: 100vw;
      height: 100vh;
      position: absolute;
      top: 0;left: 0;
      z-index: 100;
      /*background: #b7bcbf;*/
      /*background: url(../../assets/signBg.png) top left no-repeat;*/
      /*background-size: 100% 100%;*/
      .upBgWarp{
        width: 100vw;
        height: 100vh;
        position: absolute;
        top: 0;left: 0;
        background: url(../../assets/signBg.jpg) top left no-repeat;
        background-size: 100% 100%;
      }
      .signIn{
        width: 340px;
        height: 540px;
        padding: 40px;
        background: rgba(255, 88, 9, 0.5);
        position: absolute;
        top: 50%;left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: 2px 2px 16px rgba(20, 20, 20, 0.59);
        border-radius: 4px;
        overflow: hidden;
        &::after{
          content: '';
          display: inline-block;
          width: 260%;
          height: 150%;
          border-radius: 50%;
          background: #ffffff;
          position: absolute;
          top: 50%;left: 50%;
          transform: translate(-50%, 14%);
          z-index: 5;
          box-shadow: 0 0 10px rgba(87, 87, 87, 0.8);
        }
        .signText{
          height:60px;
          line-height:60px;
          font-size: 22px;
          font-weight: bold;
          letter-spacing: 4px;
          color: #ffffff;
          margin-bottom: 15px;
          text-shadow: 1px 1px 2px #000000;
        }
        .itemInput{
        }
        .inputWarp{
          position: relative;
          margin-bottom: 15px;
          .el-input-group__prepend {
            width: 85px;
          }
          .inputState{
            position: absolute;
            top: 50%;
            right: 0;
            transform: translate(120%, -50%);
          }
          .inputStateE{
            color: red;
          }
          .inputStateS{
            color: green;
          }
        }
        .el-button--primary.is-disabled, .el-button--primary.is-disabled:active, .el-button--primary.is-disabled:focus, .el-button--primary.is-disabled:hover {
          color: #FFF;
          background-color: #409EFF!important;
          border-color: #409EFF!important;
        }
      }
    }
  }
</style>



